body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: url(./spacestation.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.pic { 
  height: 270px;
  width: 270px;
}

.infobox {
  border-style: solid;
  border-width: 3px;
  border-color: white;
  width: 640px;
  margin-top: 470px;
  height: 275px;
  
  
}

.info {
  display: inline-block;
  color: white;
  margin-left: 15px;
  padding-bottom: 20px;
  
  
 
}

button {
  text-align: center;
  position: absolute;
  margin-left: 130px; 
  font-weight: bold;
  
}

p { text-align: center;}


